@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  height: 100%;
}

/* Fonts */
/* Breakpoints */
/* Colors */
.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-width: 320px;
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}

/* Fonts */
/* Breakpoints */
/* Colors */
.container {
  width: 100%;
  max-width: 1150px;
  padding: 0 30px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}

.button {
  display: block;
  padding: 15px 15px 13px 15px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: #444444;
  background-color: transparent;
  border: 2px solid #444444;
  cursor: pointer;
  transition: 0.3s ease-out;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.button:disabled {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}

.title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: grid;
    grid-template-columns: 48% 52%;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    grid-template-columns: 1fr 149px 1fr;
  }
}

.main-nav__wrapper-top {
  position: relative;
  display: flex;
  align-items: center;
  width: auto;
  margin: 0 -30px;
  padding: 36px 0 31px 0;
}
.main-nav__wrapper-top::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: block;
  height: 1px;
  width: 100%;
  background-color: #ececec;
}
@media (min-width: 768px) {
  .main-nav__wrapper-top {
    margin: 0;
    padding: 22px 0 16px 0;
  }
  .main-nav__wrapper-top::after {
    width: 0;
    height: 0;
  }
}
@media (min-width: 1150px) {
  .main-nav__wrapper-top {
    grid-column: 2/3;
    grid-row: 1/2;
    margin: 0;
    padding: 9px 0 3px 0;
  }
}

.main-nav__logo {
  cursor: pointer;
  transition: 0.3s ease-out;
}
@media (max-width: 767px) {
  .main-nav__logo {
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    transform: translate(-50%, -50%);
  }
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__logo:active {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .main-nav__logo-img {
    width: 113px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .main-nav__logo-img {
    width: 149px;
    height: 72px;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav__list {
    position: absolute;
    left: 0;
    right: 0;
    display: none;
    background-color: #ffffff;
    z-index: 3;
  }
}

.main-nav__item {
  position: relative;
  padding: 28px 30px 27px 76px;
}
@media (max-width: 767px) {
  .main-nav__item::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    height: 1px;
    width: 100%;
    background-color: #ececec;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    max-width: 265px;
  }
}

.main-nav__link {
  display: block;
  height: 100%;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  transition: 0.3s ease-out;
}
.main-nav__link[href]:hover {
  opacity: 0.6;
}
.main-nav__link[href]:active {
  opacity: 0.3;
}

.main-nav__toggle {
  position: relative;
  margin: 0 30px 0 auto;
  width: 20px;
  height: 14px;
  cursor: pointer;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle:disabled {
  opacity: 0.3;
}

.main-nav__toggle-line {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #231f20;
  transition: 0.3s ease-out;
}
.main-nav__toggle-line:nth-child(1) {
  top: -4px;
}
.main-nav__toggle-line:nth-child(2) {
  top: 2px;
}
.main-nav__toggle-line:nth-child(3) {
  top: 2px;
}
.main-nav__toggle-line:nth-child(4) {
  top: 8px;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list:not(:last-of-type) {
    display: block;
  }
}
.main-nav--opened .main-nav__toggle-line:nth-child(1) {
  transform: scaleX(0);
}
.main-nav--opened .main-nav__toggle-line:nth-child(2) {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(3) {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle-line:nth-child(4) {
  transform: scaleX(0);
}

.extra-nav {
  display: none;
}
@media (min-width: 768px) {
  .extra-nav {
    display: flex;
    grid-column: 2/-1;
    justify-self: flex-end;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .extra-nav {
    grid-column: 1/-1;
    width: 100%;
  }
}

.extra-nav__item {
  padding: 21px 0;
}
@media (min-width: 768px) {
  .extra-nav__item:not(:last-child) {
    display: none;
  }
}
@media (min-width: 1150px) {
  .extra-nav__item:not(:last-child) {
    display: block;
    margin: 0 62px 0 0;
  }
}
@media (min-width: 1150px) {
  .extra-nav__item:last-child {
    margin: 0 0 0 auto;
  }
}

@media (min-width: 768px) {
  .extra-nav__text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    color: #444444;
  }
}

@media (min-width: 1150px) {
  .extra-nav__link {
    font-size: 15px;
  }
}

.user-nav {
  top: 48%;
}
@media (min-width: 768px) {
  .user-nav {
    position: relative;
    top: unset;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    grid-column: 2/-1;
    grid-row: 1/2;
    width: auto;
    margin: 0 -50px 0 0;
    padding: 0 50px 0 0;
  }
}
@media (min-width: 1150px) {
  .user-nav {
    grid-column: 3/-1;
    padding: 0 52px 0 0;
