}

.user-nav__item {
  padding: 28px 30px 27px 30px;
}
@media (min-width: 768px) {
  .user-nav__item {
    padding: 29px 32px 27px 32px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .user-nav__item:last-child {
    padding: 29px 0 27px 32px;
  }
}

.user-nav__link {
  display: flex;
  align-items: center;
}
.user-nav__link:hover .user-nav__icon {
  color: #63d1bb;
  opacity: 1;
}

.user-nav__icon {
  margin: 0 28px 0 0;
  color: #000000;
  opacity: 0.2;
}
@media (min-width: 768px) {
